export default {
	data() {
		return {
			inputValue: ''
		}
	},
	behaviors: ['uni://form-field'],
	props: {
		type: {
			type: String,
			default: 'text'
		},
		border: {
			type: Boolean,
			default: true
		},
		value: String,
		placeholder: String,
		name: String,
		btnTitle: String,
		disabled: Boolean,
		error: String
	},
	methods:{
		onKeyInput(e) {
			this.inputValue = e.target.value
			this.$emit('modelInput', this.inputValue)
		},
		handelPhone() {
			this.$emit('btnClick', this.inputValue)
		}
	}
};
